//       start: "top 90%", // –ü–æ—á–∞—Ç–æ–∫ –∞–Ω—ñ–º–∞—Ü—ñ—ó —Ç—Ä–æ—Ö–∏ —Ä–∞–Ω—ñ—à–µ
//       toggleActions: "play none none reverse",
