// <script src="https://cdn.jsdelivr.net/npm/gsap@3.12.5/dist/gsap.min.js"></script>
// <script src="https://cdn.jsdelivr.net/npm/gsap@3.12.5/dist/Draggable.min.js"></script>

window.onload = () => {
  const track = document.querySelector(".projects-track");
  const cards = Array.from(track.children);

  // 1. –ö–ª–æ–Ω—É—î–º–æ –µ–ª–µ–º–µ–Ω—Ç–∏ –¥–ª—è –±–µ–∑–∫—ñ–Ω–µ—á–Ω–æ—Å—Ç—ñ
  cards.forEach((card) => {
    const clone = card.cloneNode(true);
    track.appendChild(clone);
  });

  // 2. –í–∏—Ä–∞—Ö–æ–≤—É—î–º–æ —à–∏—Ä–∏–Ω—É –æ–¥–Ω–æ–≥–æ –ø–æ–≤–Ω–æ–≥–æ –Ω–∞–±–æ—Ä—É –∫–∞—Ä—Ç–æ–∫
  const gap = 30; // –º–∞—î –∑–±—ñ–≥–∞—Ç–∏—Å—è –∑ gap —É CSS
  const totalWidth = track.scrollWidth / 2;

  // 3. –û—Å–Ω–æ–≤–Ω–∞ –∞–Ω—ñ–º–∞—Ü—ñ—è —Ä—É—Ö—É (Marquee)
  const marquee = gsap.to(track, {
    x: `-=${totalWidth}`,
    duration: 30,
    ease: "none",
    repeat: -1,
    modifiers: {
      x: gsap.utils.unitize((x) => parseFloat(x) % totalWidth), // –ú–∞–≥—ñ—á–Ω–∏–π —Ñ—ñ–∫—Å –¥–ª—è –±–µ–∑—à–æ–≤–Ω–æ—Å—Ç—ñ
    },
  });

  // 4. –ü–∞—É–∑–∞ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ
  track.addEventListener("mouseenter", () => marquee.pause());
  track.addEventListener("mouseleave", () => marquee.play());

  // 5. –î–æ–¥–∞—î–º–æ –º–æ–∂–ª–∏–≤—ñ—Å—Ç—å –ø–µ—Ä–µ—Ç—è–≥—É–≤–∞–Ω–Ω—è (Drag)
  if (typeof Draggable !== "undefined") {
    Draggable.create(track, {
      type: "x",
      edgeResistance: 0.65,
      onPress: () => marquee.pause(),
      onRelease: () => marquee.play(),
      // –û–±–º–µ–∂—É—î–º–æ –∑–æ–Ω—É –ø–µ—Ä–µ—Ç—è–≥—É–≤–∞–Ω–Ω—è, —â–æ–± –Ω–µ "–≤—Ç–µ–∫–ª–æ"
      bounds: { minX: -totalWidth, maxX: 0 },
    });
  }
};
