import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger"; // –î–æ–¥–∞—î–º–æ —Ü–µ–π —ñ–º–ø–æ—Ä—Ç

// –†–µ—î—Å—Ç—Ä—É—î–º–æ –ø–ª–∞–≥—ñ–Ω
gsap.registerPlugin(ScrollTrigger);

// –ö—Ä–∞—â–µ –æ–±–≥–æ—Ä–Ω—É—Ç–∏ –≤ –ø–æ–¥—ñ—é –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è DOM, —â–æ–± JS –±–∞—á–∏–≤ –µ–ª–µ–º–µ–Ω—Ç–∏
window.addEventListener("DOMContentLoaded", () => {
  // –ê–Ω—ñ–º–∞—Ü—ñ—è –¥–ª—è —Ç–µ–∫—Å—Ç—É
  gsap.from(".about-me-bg-text", {
    x: -100,
    opacity: 0,
    duration: 1.2,
    ease: "power2.out",
    scrollTrigger: {
      trigger: ".flex-container",
      start: "top 80%",
      toggleActions: "play none none reverse",
      // markers: true, // –†–æ–∑–∫–æ–º–µ–Ω—Ç—É–π—Ç–µ —Ü—é —Å—Ç—Ä–æ–∫—É, —â–æ–± –ø–æ–±–∞—á–∏—Ç–∏ –º–∞—Ä–∫–µ—Ä–∏ —Å–ø—Ä–∞—Ü—é–≤–∞–Ω–Ω—è (–¥–ª—è —Ç–µ—Å—Ç—É)
    },
  });

  // –ê–Ω—ñ–º–∞—Ü—ñ—è –¥–ª—è —Ñ–æ—Ç–æ
  gsap.from(".about-me-foto", {
    x: 100,
    opacity: 0,
    duration: 1.2,
    ease: "power2.out",
    scrollTrigger: {
      trigger: ".flex-container",
      start: "top 80%",
      toggleActions: "play none none reverse",
    },
  });
});
